// com.wixpress.ecommerce.catalog.v1.Product
export type ProtoSchemaName = string;
export type TsSchemaName = string;
export type TsFieldName = string;

// Date / string / null / never etc..
export type TsType = string;

export type LeanSchemaVariable = string;

// LeanSchema - used by the compiler
// {
//   Coupon (TsSchemaName):  {  <= (LeanSchemaField)
//     product  (TsFieldName) : {    (LeanSchemaFieldStruct)
//        schemaName: "Product"
//        schemaStruct: "Map"
//      }
//   },
//   Product: {
//      ...
//   }
// }
// A record of high level schemas (like entity.schemas)
export type LeanSchema = Record<TsSchemaName, LeanSchemaFields>;
export type LeanSchemaFields = Record<TsFieldName, LeanSchemaRef>;

/**
 * a record of schema fields, and their corresponding type, which might be
 * a reference to another lean schema (variable) or definitie serializaebl string,
 * such as 'google.protobuf.Timestamp'
 */
export type LeanSchemaRef = {
  schemaName: LeanSchemaVariable;
  schemaType: 'Map' | null;
};

// Rendered Lean schema types (Used by the serializer)
export type RenderedLeanVariableName = string;
export type RenderedLeanSchema = Record<TsSchemaName, RenderedLeanSchemaFields>;
export type RenderedLeanSchemaFields = Record<
  TsFieldName,
  RenderedLeanVariableName
>;
